.card {
  background: #fff;
  border-radius: 10px;
  border: 2px solid red;
  height: 250px;
  width: 250px;
  margin: 1rem;
  position: relative;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  border-radius: 10px;
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: black;
}

.card > .img-container > img {
  width: 100%
}

.card:hover {
  box-shadow: 0 10px 20px black, 0 10px 10px darkred;
}

.wrapper {
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  /*padding: 20px;*/
  /*justify-content: space-around;*/
  align-content: flex-start;
  overflow: auto;
}

.title-bar {
  background-image: url(/React-Memory/static/media/what-the-hex-dark.00dc0de3.png);
  width: 100%;
  /*text-align: center;*/
  margin-bottom: 30px;
  border-bottom: 1px solid red;
}

h2 {
  font-family: 'Share Tech', sans-serif;
}

.header {
  display: flex;
  justify-content: space-between;
}

.title {
  font-family: 'Share Tech Mono', monospace;
  font-weight: 400;
  font-size: 3rem;
  font-style: normal;
  -webkit-text-decoration: underline red;
          text-decoration: underline red;
  margin-left: 50px;
  color: white;
}

.scoreboard {
  font-family: 'Share Tech Mono', monospace;
  font-size: 2.5rem;
  font-weight: 400;
  font-style: normal;
  text-decoration: none;
  margin-right: 50px;
  margin-top: auto;
  margin-bottom: auto;
  color: white;
}

.score {
  color: red;
}

.board {
  text-align: center;
  width: 100%;
}

.message-board {
  font-family: 'Share Tech', sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
  font-style: normal;
  text-decoration: none;
  margin-top: auto;
  margin-bottom: 10px;
  color: white;
}



.container {
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

